@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

:root {
    --container-bg:#F8F6F4 ;
    --header-bg:#1E64C8;
    --header-text:white;
    --sub-header-bg:#ffffff ;
    --sub-header-text:#606060;
    --hoover-bg:#fffae5;
    --hoover-text:#606060;
    --text:#606060;
    --active-bg:#FFD200 ;
    --active-text:#606060;
    --accent-color:#E3F4F4;
}

body {
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
    justify-content: center;
    align-items: center;
    overflow-x: hidden;
    user-select: none;
}

.main {
    display: flex;
    justify-content: center;
    align-items: top;
    height: calc(100vh - 70px);
    margin-top: 5px;
}

.menu {
    list-style: none;
    display: flex;
    width: 100%;
    height: 50px;
    background-color: var(--header-bg);
    color: white;
    overflow: hidden;
    padding-left: 10px;
}
.menu-item {
    margin-right: 50px;
    font-size: small;
    font-weight: lighter;
    cursor: pointer;
}
.menu-item:hover {
    text-decoration: underline;
}

.container-header {
    margin-bottom: 20px;
    background:var(--header-bg);
    color: var(--header-text) ;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
}

.label {
    font-weight: bold;
    font-size: 1.2em;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.weight {
    font-weight: bolder;
    font-size: 1em;
    text-align: right;
    margin-left: auto;
  }
  .description {
    font-weight: lighter;
    text-align: left;
    margin-left: 20px;
    font-weight: lighter;
  }

.task-competency-view {
    display: flex;
    width: 1024px;
    flex-shrink: 0;
}


.task-container {
    flex: 1;
    margin: 10px;
    border-radius: 10px;
    overflow: hidden;
    background-color: var(--container-bg);
    box-shadow: rgba(0, 0, 0, .1) 0 3px 5px -1px,rgba(0, 0, 0, .07) 0 6px 10px 0,rgba(0, 0, 0, .06) 0 1px 18px 0;
    min-width: 200px;
    overflow-y: scroll;
}

.competency-container {
    flex: 3;
    margin: 10px;
    border-radius: 10px;
    overflow: hidden;
    background-color: var(--container-bg);
    box-shadow: rgba(0, 0, 0, .1) 0 3px 5px -1px,rgba(0, 0, 0, .07) 0 6px 10px 0,rgba(0, 0, 0, .06) 0 1px 18px 0;
    min-width: 400px;
    overflow-y: scroll;
}

.task {
    align-items: center;
    appearance: none;
    background-color: #fff;
    border-radius: 10px;
    border-style: none;
    box-shadow: rgba(0, 0, 0, .1) 0 3px 5px -1px,rgba(0, 0, 0, .07) 0 6px 10px 0,rgba(0, 0, 0, .06) 0 1px 18px 0;
    box-sizing: border-box;
    color: var(--text);
    cursor: pointer;
    display: flex;
    fill: currentcolor;
    font-family: "Google Sans",Roboto,Arial,sans-serif;
    font-size: 14px;
    font-weight: 500;
    height: 48px;
    justify-content: center;
    letter-spacing: .25px;
    line-height: normal;
    max-width: 100%;
    overflow: visible;
    padding: 2px 24px;
    position: relative;
    text-align: center;
    text-transform: none;
    transition: box-shadow 280ms cubic-bezier(.4, 0, .2, 1),opacity 15ms linear 30ms,transform 270ms cubic-bezier(0, 0, .2, 1) 0ms;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    width: auto;
    will-change: transform,opacity;
    z-index: 0;
    margin-left: 20px;
    margin-right: 20px  ;
}
.task:not(:first-child):not(:last-child) {
    margin-top: 10px;
    margin-bottom: 10px;
}
.task:not(.selected-task):hover {
    background: var(--hoover-bg) ;
    color: var(--hoover-text);
    transition-duration: .1s;
}
.selected-task {
    background: var(--active-bg);
    color: var(--active-text) ;
    transition-duration: .1s;
}


.competency-header {
margin-bottom: 20px;
background: var(--sub-header-bg);
color: var(--sub-header-text) ;
overflow: hidden;
display: flex;
justify-content: space-between;
align-items: center;
text-align: left;
justify-content: left;
padding: 2px 24px;
}

.competency {
align-items: center;
background-color: #fff;
border-radius: 10px;
border-style: none;
box-shadow: rgba(0, 0, 0, .1) 0 3px 5px -1px,rgba(0, 0, 0, .07) 0 6px 10px 0,rgba(0, 0, 0, .06) 0 1px 18px 0;
box-sizing: border-box;
color: var(--text) ;
display: block;
fill: currentcolor;
font-family: "Google Sans",Roboto,Arial,sans-serif;
font-size: 14px;
font-weight: 500;
justify-content: center;
letter-spacing: .25px;
line-height: normal;
max-width: 100%;
position: relative;
text-transform: none;
transition: box-shadow 280ms cubic-bezier(.4, 0, .2, 1),opacity 15ms linear 30ms,transform 270ms cubic-bezier(0, 0, .2, 1) 0ms;
z-index: 0;
margin-left: 20px;
margin-right: 20px;
overflow: hidden;
}

.competency:not(:first-child):not(:last-child) {
    margin-top: 20px;
    margin-bottom: 20px;
}

@media screen and (max-width: 1044px) {
    .task-competency-view {
        width: 95% ;
        min-width: 500px;
    }
}